import React, { Component, PropTypes } from 'react'
import Sortable from 'sortablejs';

class PageEditor extends Component {

    constructor(props) {
        super(props);
    }

    componentDidMount() {
        var el = document.getElementById('from');
        var sortable = Sortable.create(el,
        {
            group: {
                name: "shared",
                pull: "clone",
                revertClone: true
            },
            onEnd: (event) => {
                console.log(event);
            }
        });
        var el1 = document.getElementById('to');
        var sortable1 = Sortable.create(el1,
        {
            group: "shared"
        });
    }

    render() {
        return <div>
            <div style={{
                width: '50vw',
                float: 'left'
            }}>
                <ul id="to">
                    <li>item 1</li>
                    <li>item 2</li>
                    <li>item 3</li>
                </ul>
            </div>
            <div style={{
                width: '50vw',
                float: 'right'
            }}>
                <div id="from">
                    <div>item 1</div>
                    <div>item 2</div>
                    <div>item 3</div>
                </div>
            </div>
        </div>;
    }
}

export default PageEditor;


